@import './../../index.scss';

.result-container {
  display: flex;
  justify-content: center;
  // align-items: center;
  background: url("./../../assets/img/result/femida.png"), lightgray -1.287px -110.897px / 108.756% 125.49% no-repeat;
  background-size: cover;
  background-repeat: no-repeat;
  
  
  
  .title-container {
    display: flex;
    align-items: flex-start;
    margin: 176px 150px 126px 150px;
    gap: 25px;

    .title {
      display: flex;
      gap: 24px;
      // justify-content: flex-end;
// align-items: center;

      .vertical-line {
        // height: 100px;
        // display: flex;
        // // padding-left: 24px;
        // flex-direction: column;
        // align-items: flex-start;
        // border-left: 4px solid $bg-renga-brick;

        flex: 1;
        border-left: 4px solid $bg-renga-brick;
        // margin-right: 12px;
      }

      h2 {
        color: $font-haiti;
        font-family: $family-titillium-web;
        font-size: 36px;
        font-style: normal;
        font-weight: 700;
        line-height: 50.4px;
      }
    }

    .list {
      // min-width: 589px;
      // height: 336px;

      .box {
        display: inline-flex;
        // padding: 60px 40px;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        gap: 25px;
        background: black;
        background-size: cover;
        background-repeat: no-repeat;
        padding: 60px 40px;

        .item {
          display: flex;
          justify-content: center;
          align-items: center;
          gap: 15px;
  
          .check-svg {
            width: 16px;
            height: 13px;
            background: url("./../../assets/img/result/check-mark.png");
          }
  
          .info-box {
  
            p {
              color: $font-white;
              font-family: $family-inter;
              font-size: 21px;
              font-style: normal;
              font-weight: 300;
              line-height: 29.4px;
            }
          }
        }
      }
    }
  }
}